import React from 'react';
import {graphql, Link} from 'gatsby';
import styled from '@kevinastone/theme-blog/styled';
import {rhythm} from '@kevinastone/theme-blog';

const Heading = styled.h3`
  margin-bottom: ${rhythm(0.25)};
`;

export default ({blog}) => {
  return (
    <div>
      <Heading>
        <Link to={blog.fields.slug}>
          {blog.frontmatter.title || blog.fields.slug}
        </Link>
      </Heading>
      <small>{blog.frontmatter.date}</small>
      <p>{blog.frontmatter.summary || blog.excerpt}</p>
      <hr />
    </div>
  );
};

export const query = graphql`
  fragment BlogSummary on MarkdownRemark {
    excerpt(format: PLAIN)
    fields {
      slug
    }
    frontmatter {
      title
      date(formatString: "MMMM DD, YYYY")
      tags
      summary
    }
  }
`;
